.cbp-l-slider-testimonials-wrap {
    background: #edeef0;
    text-align: center;
    padding: 8.5% 0;
}
.cbp-l-slider-testimonials-wrap .testimonials-slider {
    max-width: 980px;
    margin: 0 auto;
}
.testimonials-head h1 {
    text-transform: uppercase;
    font-size: 60px;
    color: transparent;
    -webkit-text-stroke-width: 2px;
    -webkit-text-stroke-color: #333333;
}
.testimonials-head {
    padding: 5% 0;
}
.border-bottom-small {
    border-bottom: 4px solid #f54359;
    width: 15%;
    margin: 0 auto;
    margin-top: 15px;
}
.slider-content-main h3 {
    color: #f54359;
    padding: 10px 0;
    font-weight: 500;
    font-size: 20px;
}
.slider-content-main h3 {
    color: #f54359;
    padding: 10px 0;
    font-weight: 500;
    font-size: 20px;
}
.slider-content-main p {
    font-size: 22px;
    font-weight: 600;
    line-height: 1.5;
}
.slider-name-author {
    padding: 25px 0;
}
.slider-name-author h2 {
    font-size: 18px;
    text-transform: uppercase;
}
.slider-name-author h5 {
    font-size: 15px;
    text-transform: capitalize;
    color: #f54359;
    margin-top: 15px;
}
.slider-content-main {
    padding: 0 7%;
    position: relative;
}
.slider-content-main:before {
    background-image: url(../Images/icon-left.png);
    content: "";
    display: block;
    float: left;
    margin: 0 6px 0 0;
    background-position: center;
    height: 50px;
    width: 50px;
    background-size: contain;
    background-repeat: no-repeat;
    position: absolute;
    left: 0;
    top: 0;
}
.slider-content-main:after {
    background-image: url(../Images/icon-right.png);
    content: "";
    display: block;
    float: left;
    margin: 0 6px 0 0;
    background-position: center;
    height: 50px;
    width: 50px;
    background-size: contain;
    background-repeat: no-repeat;
    position: absolute;
    right: 0;
    bottom: 80px;
}
.slick-slide * {
    outline: none;
}