.footer {
    background: #f54359;
    padding-bottom: 1%;
}
.footer-content {
    padding: 5% 0 10px 0px;
}
.footer-content h1 {
    font-size: 55px;
    color: transparent;
    padding: 10px 0;
    -webkit-text-stroke-width: 2px;
    -webkit-text-stroke-color: white;
}
.footer-content p {
    color: #fff;
    line-height: 1.5;
    font-size: 25px;
    padding-bottom: 10px;
}
.footer-content h4 {
    font-weight: 500;
    font-size: 21px;
    color: #333333;
}
.footer-content a {
    text-decoration: none;
}
.border-bottom-white {
    border-bottom: 1px solid #fff;
}
.social-footer ul li a, .social-footer ul{
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-flex-direction: row;
    -ms-flex-direction: row;
    flex-direction: row;
    -webkit-justify-content: flex-start;
    -ms-flex-pack: flex-start;
    justify-content: flex-start;
        align-items: center;
        text-decoration: none;

}
.social-footer ul li {
    list-style: none;
    padding: 5px 0;
        width: 25%;
}
.social-footer {
    padding: 10px 0;
}
.social-footer .social-icon{
    width: 10%;
}
.social-footer .social-content{
    width: 90%;
}
.social-icon svg,.social-icon img {
    width: 25px;
    height: 25px;
}
.social-icon svg path{
    fill: #fff;
}
.social-content p {
    color: #fff;
    margin-left: 10px;
}