.header-menu ul {
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-flex-direction: row;
    -ms-flex-direction: row;
    flex-direction: row;
    -webkit-justify-content: center;
    -ms-flex-pack: space-between;
    justify-content: flex-end;
}
.header-menu ul li {
    list-style: none;
    transition: .5s ease-in-out;
    -moz-webkit-transition: .5s ease-in-out;
}
.header-bg-img {
    background-image: url(../Images/banner-bg.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height: 100vh;
}
.header-menu ul li a {
    text-decoration: none;
    color: #fff;
    font-size: 12px;
    text-transform: uppercase;
    font-weight: 600;
    padding: 30px 15px;
    display: inline-block;
}
.header-menu ul li:hover {
    background-color: #f54359;
}
.border-bottom {
    border-bottom: 1px solid #f54359;
}
.header-content {
    max-width: 100%;
    margin-top: 10%;
}
.header-content h1 {
    color: #f54359;
    text-transform: uppercase;
    font-size: 4em;
    line-height: 1.3;
}
.rohit-design{
    -webkit-text-stroke-width: 2px;
    -webkit-text-stroke-color: #f54359;
    color: transparent;
}
.header-content h2 {
    color: #fff;
    font-weight: 500;
    font-size: 3em;
    margin-top: 10px;
}
.header-content h2 span{
    color: #f54359;
}
.header-content p {
    color: #fff;
    font-size: 22px;
    line-height: 1.8;
    margin-top: 20px;
}