.portfolio-desc-section {
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-flex-direction: row;
    -ms-flex-direction: row;
    flex-direction: row;
    -webkit-justify-content: center;
    -ms-flex-pack: space-between;
    justify-content: space-between;
}
.portfolio-desc-section .portfolio-items {
    width: 25%;
    position: relative;
}
.portfolio-desc-section .portfolio-items img {
    width: 100%;
}
.portfolio-desc-section .portfolio-items .portfolio-item-content {
    padding: 0 10px;
}
.portfolio-item-content a {
    display: inline-block;
    padding-bottom: 20px;
}
.portfolio-overlay .overlay-text {
    position: absolute;
    left: 0;
    right: 0;
    text-align: center;
    color: #000;
    z-index: 1;
    top: 50%;
    transform: translateY(-50%);
}
.portfolio-overlay {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: rgba(239, 147, 153, 0.5);
    opacity: 0;
    -webkit-transition: all 0.5s ease;
    -moz-transition: all 0.5s ease;
    transition: all 0.5s ease;
}
.portfolio {
    padding-top: 5%;
} 