*{margin: 0;padding: 0;}
body {
  margin: 0;
  font-family: 'Josefin Sans', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.wrapper {
  width: 100%;
}
.container {
  max-width: 1170px;
  margin: 0 auto;
}
polygon {
  fill: #fff;
}
/*-=-=-=-=-Responsive-CSS-=-=-=-=-*/
@media only screen and (max-width: 1024px) {
  .header-content {
      padding: 0 30px;
  }
  .header-content h1 {
      font-size: 4.5em;
  }
  .skills {
      padding-left: 15px;
  }
  .logos {
      padding-left: 15px;
  }
  .custom-footer-section {
      padding-left: 20px;
  }
  .skills-content p {
      font-size: 22px;
  }
  .social-footer ul li {
      width: 50%;
  }
}
@media only screen and (max-width: 950px) {
  .skills-content p {
      line-height: 1.4;
  }
  .about-content p {
      font-size: 20px;
  }
}
@media only screen and (max-width: 767px) {
  .header-content h1 {
      font-size: 4em;
  }
  .header-content p {
      font-size: 18px;
  }
  .slider-content-main p {
      font-size: 20px;
  }
  .cbp-l-slider-testimonials-wrap {
      padding: 8.9% 0;
  }
  .footer-content h1 {
      font-size: 50px;
  }
  .footer-content p {
      font-size: 22px;
  }
  .footer-content h4 {
      font-size: 18px;
  }
  .portfolio-desc-section .portfolio-items {
      width: 50%;
  }
  .skills-img img {
      width: 80%;
  }
  .skills ul li span {
      font-size: 22px;
      margin-left: 5px;
      line-height: 1.4;
  }
  .skills-content p {
      font-size: 18px;
  }
  .header-menu ul li a {
      font-size: 14px;
  }
  .about-content {
      padding: 35px 30px;
  }
  .about-content p {
      font-size: 18px;
      margin-bottom: 10px;
  }
}
@media only screen and (max-width: 580px) {
  .header-bg-img {
      height: 90vh;
  }
}
@media only screen and (max-width: 480px) {
  .header-bg-img {
      height: 100vh;
  }
  .about-skills-section .skills-section {
      width: 100%;
  }
  .about-skills-section .about-us-section {
      width: 100%;
  }
  .header-content h1 {
      font-size: 3em;
  }
  .header-content p {
      font-size: 22px;
  }
  .header-menu ul {
      justify-content: center;
  }
  .header-menu ul li {
      padding: 10px 5px 10px 5px;
  }
  .portfolio-desc-section .portfolio-items {
      width: 100%;
  }
  .header-menu ul li a {
      font-size: 14px;
  }
  .skills ul li span {
      font-size: 20px;
      margin-left: 10px;
  }
  .slider-content-main:before {
      left: 10px;
      top: 10px;
      height: 30px;
      width: 30px;
  }
  .slider-content-main:after {
      right: 0;
      bottom: 80px;
      height: 30px;
      width: 30px;
  }
  .portfolio-desc-section .portfolio-items .portfolio-item-content {
      padding: 20px;
  }
  .about-skills {
      background: none;
  }
  .header-content {
      padding: 0 10px;
  }
  .header-content {
      margin-top: 20%;
  }
  .rohit-design {
      -webkit-text-stroke-width: 1px;
  }
  .footer {
      padding: 5% 0;
  }
  .footer-content h1 {
      line-height: 1.2;
  }
  .social-footer ul li {
      width: 100%;
  }
}
@media only screen and (max-width: 400px){
  .header-menu ul li a {
  font-size: 12px;
}
.slider-content-main:before {
      display: none;              
  }
  .slider-content-main:after {
      display: none;
  }
  .header-content h1 {
      font-size: 2.5em;
  }
  .header-content h2  {
      font-size: 2em;
      margin-top: 0;
  }
  .header-content p {
      font-size: 18px;
  }   
  .header-content {
      margin-top: 30%;    
  }
}
@media only screen and (max-width: 350px){
  .header-menu ul li {
    padding: 10px 0;
  }
}