.about-skills {
    background: #f54359;
    background: linear-gradient(90deg, #FFF 65%, #f54359 35%);
}
.about-skills-section {
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-flex-direction: row;
    -ms-flex-direction: row;
    flex-direction: row;
    -webkit-justify-content: center;
    -ms-flex-pack: space-between;
    justify-content: flex-end;
}
.about-skills-section .skills-section {
    width: 50%;
}
.about-skills-section .about-us-section {
    width: 50%;
}
.logos .about-framework ul {
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-flex-direction: row;
    -ms-flex-direction: row;
    flex-direction: row;
    -webkit-justify-content: center;
    -ms-flex-pack: space-between;
    justify-content: space-between;
}
.about-framework ul li {
    list-style: none;
}
.logo-line-1 ul li {
    width: 25%;
}
.logos .logo-line-1 ul li img {
    width: 80%;
}
.logos .logo-line-2 ul li img {
    width: 80%;
}
.logo-line-2 .logo-small {
    width: 25%;
}
.logo-line-2 .logo-singles {
    width: 50%;
    text-align: center;
}
.logo-line-1 {
    margin-bottom: 1em;
}
.logos {
    margin-top: 5%;
}
.about-us-section {
    background: #f54359;
}
.about-content {
    padding: 48px 40px;
}
.about-content h2 {
    font-size: 2em;
    color: #fff;
    text-transform: capitalize;
    padding-bottom: 10px;
}
.about-content p {
    color: #fff;
    font-size: 20px;
    line-height: 1.3;
    margin-bottom: 15px;
}
.skills {
    margin-top: 3em;
}
.skills h1 {
    text-transform: capitalize;
    font-size: 2em;
    margin-bottom: 15px;
}
.skills ul {
    margin-top: 25px;
}
.skills ul li {
    margin-bottom: 16px;
    list-style: none;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-flex-direction: row;
    -ms-flex-direction: row;
    flex-direction: row;
    -webkit-justify-content: flex-start;
    -ms-flex-pack: flex-start;
    justify-content: flex-start;
    align-items: center;
}
.skills-content p {
    font-size: 24px;
}
.skills-img {
    width: 10%;
}
.skills-content {
    width: 90%;
}
.skills-img img {
    width: 80%;
}